import React, { useMemo } from 'react'
import { getHeroesByPublisher } from '../../selector/getHeroesByPublisher'
import { HeroCard } from './HeroCard';

export const HeroList = ({ publisher }) => {

    //UTILIZAMOS USE MEMO PARA OPTIMIZAR RENDIMIENTO PARA NO RENDERIZAR COMPONENTES SI NO CAMBIA

    const heroes = useMemo(() => getHeroesByPublisher(publisher), [ publisher ]);

    return (

        <div className='container'>
            <div className="row row-cols-md-2 row-cols-lg-3 row-cols-sm-1 g-4">

            {
                heroes.map( heroe => (
                    
                    <HeroCard 
                        key={heroe.id} 
                        {...heroe}
                    />
                        
                ))
            }
        
            </div>
        </div>
        

    )
}
