import React from 'react'
import { Link } from 'react-router-dom'
import './styles.css'

export const HeroCard = ({ id, superhero, alter_ego, first_appearance, characters }) => {

    return (

            <div className="col">
                <div className="card mb-3 ms-0" style={{maxWidth:540}} >
                    <div className="row g-0">
                        <div className="col-md-6">
                            <img  src={`./assets/heroes/${id}.jpg`} className='img-fluid rounded float-start' alt={superhero} />
                        </div>
                        <div className="col-md-6">
                            <div className="card-body">
                                <h5 className="card-title">{superhero} </h5>
                                <p className="card-text">{alter_ego}</p>

                                {
                                    (alter_ego !== characters)
                                    && <p className='card-text'> {characters}</p>
                                }

                                <p className="card-text"><small className="text-muted">{first_appearance}</small></p>
                                <Link  to={`./heroe/${id}`}> Mas... </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

    )
}


